.side-drawer {
    height: 100%;
    width: 120px;
    position: fixed;
    top: 0;
    left: 0;
    background-color: black;
    overflow-x: hidden;
    z-index: 1;
    padding-top: 15px;
    transform: translateX(-100%);
    transition: transform 0.8s ease-out;
} 

.side-drawer.slide-action {
    transform: translateX(0);
}

.hamburger, .x {
    position: absolute;
    cursor: pointer;
    margin-left: 22px;
    margin-top: 7px;
}

.hamburger span, .x span {
    position: relative;
    width: 39px;
    height: 5px;
    background: red;
    display: block;
    margin: 6px;
    transition: 0.8s;
}

.hamburger {
    transform: translateX(0);
    transition: 0.8s;
}

.x {
    transform: translateX(110px);
    transition: 0.8s;
}

.x span:nth-child(1) {
    transform: rotate(315deg);
    top: 10px;
    transition: 0.8s;
    background: rgba(233, 38, 20, 0.897);
}

.x span:nth-child(2) {
    transform: rotate(-315deg);
    bottom: 1px;
    transition: 0.8s;
    background: rgba(233, 38, 20, 0.897);
}

.x span:nth-child(3) {
    transform: scaleX(0) rotate(90deg);
    transition: 0.8s;
    background-color: black;
    background: rgba(233, 38, 20, 0.897);
}

.fa-home, .fa-user, .fa-eye, .fa-address-book {
    font-size: 1.9em;
    color: rgba(233, 38, 20, 0.897);
    position: relative;
    margin-top: 20px;
}

.fa-home:hover, .fa-user:hover, .fa-eye:hover, .fa-address-book:hover {
    transform: scale(1.2);
    color: white;
}

